body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(225, 222, 224, 0.5);
  /* width: 70%; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


:root {
    --mainbrand-color: rgb(29, 97, 143);
    --secodary-color: black;

    --main-bg-color:  rgba(228, 220, 215);
    --main-line-color: rgba(41, 30, 45);
    --main_text-color: rgba(40, 30, 45);
    --navbar_text-color: rgb(104, 166, 150);
  }
  
  .mainbackground-color {
    background-color: rgba(228, 220, 215);
    background-color: var(--main-bg-color);
  }


  
  .listz-page {
    background-color: rgba(228, 220, 215);
    background-color: var(--main-bg-color);
    color: red;
  }

  .navbartext-color  a{
    color: rgb(104, 166, 150) !important;
    color: var(--navbar_text-color) !important;
  }

  nav .navbar-nav li a{
    color: rgb(104, 166, 150) !important;
    color: var(--navbar_text-color) !important;
  }
  
  .mainbrand-color {
    background-color: rgb(29, 97, 143);
    background-color: var(--mainbrand-color);
  }

  .mainline-color {
    background-color: rgba(41, 30, 45);
    background-color: var(--main-line-color);
  }
  
  .textcolor_accent {
    color: rgba(40, 30, 45);
    color: var(--main_text-color);
  }

  .textcolor_navbar {
    color: rgba(40, 30, 45);
    color: var(--main_text-color);
  }
  

  .textcolor_black {
    color: black;
  }

  .page-div {
    background-color: white;
  }
  


.navbar.navbar-default {
  border-bottom: 0;
}
